* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.home-left {
  /* box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
    width: 100%;
    padding: 15px;
    border-radius: 7px;
    border: 1px solid #eee; */
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  width: 49%;
  padding: 15px;
  border-radius: 7px;
  border: 1px solid #eee;
  float: left;
  background: #fff;
}

.boxRed {
  border-left: 5px solid red !important;
}

.boxGreen {
  border-left: 5px solid green !important;
}

.box-msg {
  position: relative;
  top: 45%;
  text-align: center;
  word-break: break-word;
  left: 10%;
  width: 80%;
  display: block;
}

.box-container {
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  border-radius: 10px;
  padding: 10px;
  border: 1px solid #eee;
  font-size: 12px;
  position: relative;
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.clickBtn {
  margin-left: 1rem;
  width: 20%;
  background: #f26323;
  color: #fff;
  border: 1px solid #f26323;
  border-radius: 5px !important;
}

.box-inner {
  height: calc(100vh - 184px);
  overflow-y: auto;
}

.boxTwo {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.showHide {
  white-space: nowrap !important;
}

.boxTwo p:first-child {
  width: 95%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.boxTwo p:last-child {
  cursor: pointer;
}

.decBtn button {
  min-width: 85px;
  background: #fff;
  border-radius: 8px !important;
  height: 1.6rem !important;
  font-weight: 600;
}

.decBtn p {
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  /* color: #24c624; */
}

.decBtn {
  position: absolute;
  right: 0;
  top: 0;
}

.box-container p {
  margin-bottom: 0.3rem;
}

.box-container .boxTwo p {
  margin-bottom: 0;
}

.seeAll {
  position: absolute;
  bottom: 50px;
  left: 15%;
  z-index: 99;
  min-width: 200px;
  width: 200px;
}

@media screen and (max-width: 767px) {
  .home-left {
    width: 100%;
    background: #fff;
  }

  .seeAll {
    left: 16% !important;
  }
}